@font-face {
  font-family: Apercu;
  src: url("Apercu-Medium.6d8ab548.woff");
}

@keyframes tagline {
  0%, 18% {
    transform: translateY(-105%);
  }

  26%, 100% {
    transform: translateY(0);
  }
}

@keyframes logo {
  0%, 12% {
    transform: translateY(105%);
  }

  22%, 100% {
    transform: translateY(0);
  }
}

*, :after, :before {
  box-sizing: border-box;
}

:root {
  font-size: 18px;
}

body {
  --color-text: #fff;
  --color-bg: #000;
  --color-link: #ffed00;
  --color-link-hover: #fff;
  color: var(--color-text);
  background-color: var(--color-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: Apercu, sans-serif;
}

h1, h3 {
  margin: 0;
}

.frame {
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.content {
  text-align: center;
  width: 80vw;
  max-width: 560px;
  overflow: hidden;
}

.content .tagline {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 24px;
  font-size: 14px;
  animation-name: tagline;
  animation-duration: 10s;
}

.content .logo {
  animation-name: logo;
  animation-duration: 10s;
}

.content .logo img {
  width: 100%;
  height: auto;
}

@media screen and (width >= 768px) {
  .content .tagline {
    font-size: 16px;
  }
}

#canvasContainer {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#canvasContainer img {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
}

