@font-face {
	font-family: 'Apercu';
	src: url('../fonts/Apercu-Medium.woff');
}

@keyframes tagline {
	0%, 18% {transform: translateY(-105%)}
	26%, 100% {transform: translateY(0)}
}

@keyframes logo {
	0%, 12% {transform: translateY(105%)}
	22%, 100% {transform: translateY(0)}
}

*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	font-size: 18px;
}

body {
	margin: 0;
	--color-text: #fff;
	--color-bg: #000;
	--color-link: #FFED00;
	--color-link-hover: #fff;
	color: var(--color-text);
	background-color: var(--color-bg);
	font-family: 'Apercu', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

h1, h3 {
	margin: 0
}

.frame {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.content {
	overflow: hidden;
	text-align: center;
	width: 80vw;
	max-width: 560px;
}

.content .tagline {
	text-transform: uppercase;
	font-size: 14px;
	margin-bottom: 24px;
	letter-spacing: 2px;
	animation-name: tagline;
	animation-duration: 10s;
}

.content .logo {
	animation-name: logo;
	animation-duration: 10s;
}

.content .logo img {
	width: 100%;
	height: auto;
}

@media screen and (min-width: 768px) {
	.content .tagline {
		font-size: 16px;
	}
}

#canvasContainer{
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
}
#canvasContainer img{
	visibility: hidden;
	pointer-events: none;
	position: absolute;
}
